import React from 'react';
import {useSelector} from 'react-redux';
import container from "../../../../core/container/singletons/container";

function LoaderWidget(props) {

    let state = useSelector(store => store['loaderStatus']);

    // let state = props.loaderStatus;

    let LoaderWidget = props['type'];
    let isLoading;
    if (state.hasOwnProperty('isLoading')) {
        isLoading = state.isLoading;
    } else {
        isLoading = container.get('loader.services.status').isLoading(props.name);
    }
    return (
        isLoading ? (
            LoaderWidget ? (
                <LoaderWidget/>
            ) : (
                <div>Loading....</div>
            )
        ) : (
            props.children
        )
    );
}

export default React.memo(LoaderWidget);
