
class HelpConfigurator {

    private collection = [];

    getCollection() {
        return this.collection;
    }

    addCollection(domainName, collection) {
        for (let i in collection) {
            if (collection.hasOwnProperty(i)) {
                let entity = collection[i];
                this.addEntity(domainName, entity);
            }
        }
    }

    addEntity(domainName, entity) {
        entity['domainName'] = domainName;
        this.collection.push(entity);
    }
}

// eslint-disable-next-line
export default new HelpConfigurator();
