import React, {useState} from "react";
import showHelpInModal from "./showHelpInModal";
import ArticleService from "../../../domain/services/ArticleService";
import container from "../../../../../core/container/singletons/container";
import SoftLink from "../../../../../widgets/link/SoftLink";

export default function HelpModalLink(props) {
    const [title, setTitle] = useState(props.children);

    let name = props.name;
    // let title = props.children;
    if (!title) {
        let articleService: ArticleService = container.get("help.services.article");
        // let menuItem = articleService.findOneByName(name);
        // title = menuItem.title;

        articleService.oneById(name).then((menuItem) => {
            setTitle(menuItem['title']);
        });
    }
    let options = {
        showBreadcrumbs: true,
        showMenu: true,
    };
    Object.assign(options, props);
    let onClickMenu = () => showHelpInModal(name, options);
    return (
        <SoftLink onClick={onClickMenu}>
            {title}
        </SoftLink>
    );
}
