import ArticleRepository from "../repositories/array/ArticleRepository";
import BaseCrudService from "../../../../core/domain/services/BaseCrudService";
import MarkdownRenderServiceInterface from "../../../markdown/interfaces/MarkdownRenderServiceInterface";
import Query from "../../../../core/domain/libs/Query";

export default class ArticleService extends BaseCrudService {

    protected articleRepository: ArticleRepository;
    protected markdownRender: MarkdownRenderServiceInterface;

    constructor(articleRepository: ArticleRepository, markdownRenderService: MarkdownRenderServiceInterface) {
        super(null, articleRepository);
        this.articleRepository = articleRepository;
        this.markdownRender = markdownRenderService;
    }

    /*async findOneById(name) {
        let entity = this.articleRepository.findOneByName(name);
        this.prepareEntity(entity);
        return entity;
    }*/

    /*findOneByName(name) {
        let entity = this.articleRepository.findOneByName(name);
        this.prepareEntity(entity);
        return entity;
    }*/

    async oneById(id, query: Query = null): Promise<any> {
        let entity = await super.oneById(id, query);
        this.prepareEntity(entity);
        return entity;
    }

    protected prepareEntity(entity) {
        if (entity.contentMd) {
            entity.contentHtml = this.markdownRender.render(entity.contentMd);
        }
    }
}
