import Query from "../../../../../core/domain/libs/Query";
import path from "path";
import _ from "lodash";
import RestClient from "../../Libs/RestClient";
import ArticleEntity from "../../entities/ArticleEntity";

export default class ArticleRepository {

    constructor(private readonly restClient: RestClient) {
    }

    async oneById(id, query: Query = null) {
        id = ArticleRepository.canonicalPath(id);
        return new Promise((resolve, reject) => {
            try {
                this.findOneBy(id).then((entity: ArticleEntity) => {
                    resolve(entity);
                });
            } catch (e) {
                reject(e);
            }
        });
    }

    private static canonicalPath(file: string): string {
        if (!file) {
            file = '';
        }
        file = _.trim(file, '/');
        if (!path.extname(file)) {
            file = file + '/README.md';
        }
        return file;
    }

    private async findOneBy(id) {
        let url = id;
        try {
            let axiosResponse = await this.restClient.request(url);
            let contentTypeData = _.split(axiosResponse.headers['content-type'], ';');
            let mimeType = contentTypeData[0].trim();
            return new ArticleEntity(id, axiosResponse.data, mimeType);
        } catch (e) {
            return new ArticleEntity(id, e.message);
        }
    }
}
