import eventEmitter from "../../../../packages/core/event/singletons/eventEmitter";
import PageTitleEventEnum from "../../../../packages/ext/app/enums/PageTitleEventEnum";
import pageHead from "../../../../packages/ext/pageTitle/singletons/pageTitle";
import breadcrumbFacade from "../../../../packages/bundles/breadcrumb/domain/facades/breadcrumbFacade";

let currentUrl = null;
eventEmitter.on(PageTitleEventEnum.BEFORE_RENDER, function (location: Location) {
    let uri = location.pathname + location.search;
    if (currentUrl !== uri) {
        currentUrl = uri;
        pageHead.setPageTitleDefault();
        breadcrumbFacade.addHome();
    }
});
