import React from 'react';
import container from "../../../../../packages/core/container/singletons/container";
import ArticleService from "../../domain/services/ArticleService";
import BaseItemComponent from "../../../../../packages/ext/react/base/BaseItemComponent";
import ArticleItemView from "../views/articleItem";

class ArticleItemComponent extends BaseItemComponent {

    protected service: ArticleService = container.get('mdReader.services.article');

    componentDidUpdate(prevProps) {
        this.checkUpdate(prevProps);
    }

    initBreadcrumbs(): void {
        this.addBreadcrumb('MD Reader', '/md-reader/article');
        if (this.getEntity()) {
            let id = this.getItemId();
            let title = this.getEntity().title;
            this.addBreadcrumb(title, '/md-reader/article/' + id);
            this.setTitle(title);
        }
    }

    getItemId() {
        let file = this.props["match"].params[0];

        return file;
    }

    render() {
        return (
            <ArticleItemView entity={this.getEntity()}/>
        );
    }
}

export default ArticleItemComponent;
