import React from "react";
import HelpModalLink from "../modalLink/HelpModalLink";

export default function ItemBreadcrumbs(props) {
    if (!props.items) {
        return null;
    }
    return (
        <ol className="breadcrumb">
            {props.items.map(function (menuItemName, index) {
                return (
                    <li className="breadcrumb-item" key={index}>
                        <HelpModalLink name={menuItemName}/>
                    </li>
                );
            })}
        </ol>
    );
}
