import diConfigurator from "../../../../core/container/singletons/diConfigurator";
import ArticleRepository from "../repositories/array/ArticleRepository";
import ArticleService from "../services/ArticleService";

export default function configureDi(domainName: string) {
    let bundleDiConfigurator = diConfigurator.createInstance(domainName);

    bundleDiConfigurator.bind("repositories.array.article", ArticleRepository);
    bundleDiConfigurator.bind(
        "services.article",
        ArticleService,
        [
            "this.repositories.array.article",
            "markdown.services.render",
        ]
    );
}
