import React from "react";

export default function SoftLink(props) {
    let subProps = {};
    Object.assign(subProps, props);
    subProps['href'] = '';
    subProps['onClick'] = (e) => {
        e.preventDefault();
        props.onClick();
    };
    return (
        <a {...subProps}>
            {props.children}
        </a>
    );
}
