import _ from "lodash";
import helpConfigurator from "../../singletons/helpConfigurator";
import DataProvider from "../../../../../core/domain/libs/DataProvider";
import Query from "../../../../../core/domain/libs/Query";
import Paginator from "../../../../../core/domain/libs/Paginator";
import HelpEntityHelper from "../../helpers/HelpEntityHelper";
import ReadRepositoryInterface from "../../../../../core/domain/interfaces/repository/packs/ReadRepositoryInterface";
import BaseCrudArrayRepository from "../../../../../core/domain/base/array/BaseCrudArrayRepository";

export default class ArticleRepository extends BaseCrudArrayRepository implements ReadRepositoryInterface {

    primaryKey(): string {
        return 'name';
    }

    getItems(): object[] {
        let collection = helpConfigurator.getCollection();
        for (let i in collection) {
            let entity = collection[i];
            entity = HelpEntityHelper.prepareEntity(entity.domainName, entity);
        }
        if (!this.isHasCollection()) {
            this.collection = collection;
        }
        return this.collection;
    }

    /*async findOneById(name) {
        let query = new Query();
        query.addFilter('name', name);

        let promise = new Promise((resolve, reject) => {
            let entity = this.findOneBy(query);
            resolve(entity);
        });
        return promise;
    }*/

    /*findOneByName(name) {
        let query = new Query();
        query.addFilter('name', name);
        return this.findOneBy(query);
    }*/

    /*async oneById(id, query: Query = null) {
        return this.findOneByName(id);
    }*/

    async all(query: Query = null) {
        let collection = this.getItems();
        let level = query.getFilterByName('level');
        if (level === 0) {
            let callback = function (entity) {
                if (entity.breadcrumbs) {
                    return entity.breadcrumbs.length === 1 && entity.name === entity.breadcrumbs[0];
                }
                return false;
            };
            collection = _.filter(collection, callback);
        }

        let dataProvider = new DataProvider();
        dataProvider.collection = collection;
        dataProvider.paginator = this._createPaginatorFromRequestMeta({totalCount: collection.length});
        dataProvider.query = query;
        return dataProvider;
    }

    async getDataProvider(query: Query = null) {
        return this.all(query);
    }

    protected _createPaginatorFromRequestMeta(meta) {
        let paginator = new Paginator();
        paginator.page = meta.page || 1;
        paginator.perPage = meta.perPage || 1000;
        paginator.totalCount = meta.totalCount || 0;
        return paginator;
    }
}
