import React from "react";

export default function ItemMenu(props) {
    if (!props.items) {
        return null;
    }
    let Link = props.linkWidget;
    return (
        <>
            <h3>Menu</h3>
            <ul>
                {props.items.map(function (menuItemName, index) {
                    return (
                        <li key={index}>
                            <Link name={menuItemName}/>
                        </li>
                    );
                })}
            </ul>
        </>
    );
}
