import React from 'react';
import Html from "../../../../../widgets/html/Html";
import {Button} from "react-bootstrap";
import HelpModalLink from "../../../../help/web/widgets/modalLink/HelpModalLink";

export default function ArticleItemView(props) {
    let entity = props.entity;
    if (entity) {
        return (
            <>
                <span className="float-right">
                    <HelpModalLink name='mdReader.index'>
                        <Button variant="secondary">?</Button>
                    </HelpModalLink>
                </span>
                <Html>
                    {entity.content}
                </Html>
            </>
        );
    }
    return null;
}
