import BaseArrayRepository from "./BaseArrayRepository";
import Query from "../../libs/Query";
import DataProvider from "../../libs/DataProvider";
import Paginator from "../../libs/Paginator";
import _ from "lodash";

export default abstract class BaseCrudArrayRepository extends BaseArrayRepository {

    primaryKey(): string {
        return 'id';
    }

    getItems(): object[] {
        return this.collection;
    }

    /*async oneById(id, query: Query = null) {
        query = this.forgeQuery(query);
        query.addFilter(this.primaryKey(), id);
        return this.findOneBy(query);
    }*/

    protected forgeQuery(query) {
        if (query === null) {
            query = new Query();
        }
        return query;
    }

    getFilterCallback(query: Query) {
        return function (entity) {
            for (let filterAttr in query.filter) {
                if (query.filter.hasOwnProperty(filterAttr)) {
                    let filterValue = query.filter[filterAttr];
                    if (filterValue !== entity[filterAttr]) {
                        return false;
                    }
                }
            }
            return true;
        };
    }

    findOneBy(query: Query) {
        let callback = this.getFilterCallback(query);
        return _.cloneDeep(_.find(this.getItems(), callback));

        /*return new Promise((resolve, reject) => {
            let entity = _.find(this.getItems(), callback);
            resolve(entity);
        });*/
    }

    /*async findOneById(name) {
        let query = new Query();
        query.addFilter(this.primaryKey(), name);
        return this.findOneBy(query);
    }*/

    async oneById(id, query: Query = null) {
        query = this.forgeQuery(query);
        query.addFilter(this.primaryKey(), id);
        return new Promise((resolve, reject) => {
            try {
                let entity = this.findOneBy(query);
                resolve(entity);
            } catch (e) {
                reject(e);
            }
        });
    }

    /*async oneById(id, query: Query) {
        query = this.forgeQuery(query);
        query.addFilter(this.primaryKey(), id);
        return this.findOneBy(query);
    }*/

    async getDataProvider(query: Query = null) {
        try {
            let dataProvider = new DataProvider();
            dataProvider.collection = this.getItems();
            dataProvider.paginator = this.createPaginatorFromRequestMeta();
            dataProvider.query = query;
            return dataProvider;
        } catch (error) {
            throw error;
        }
    }

    protected createPaginatorFromRequestMeta() {
        let paginator = new Paginator();
        paginator.page = 1;
        paginator.perPage = 99999999;
        paginator.totalCount = this.getItems().length;
        return paginator;
    }

    /*all() {
        return [

        ];
    }*/
}
