import BaseCrudService from "../../../../../packages/core/domain/services/BaseCrudService";
import Query from "../../../../core/domain/libs/Query";
import ArticleEntity from "../entities/ArticleEntity";
import HtmlContentHelper from "../helpers/HtmlContentHelper";
import PluginManager from "../Libs/PluginManager";

export default class ArticleService extends BaseCrudService {

    constructor(storeDispatcher = null, repository, private readonly pluginManager: PluginManager) {
        super(storeDispatcher, repository);
    }

    async oneById(id, query: Query = null): Promise<any> {
        let articleEntity: ArticleEntity = await super.oneById(id, query);
        this.pluginManager.onAfterRead(articleEntity);
        articleEntity.title = HtmlContentHelper.extractTitleFromHtml(articleEntity.content);
        if (articleEntity.title == null) {
            articleEntity.title = 'Empty title';
        }
        return articleEntity;
    }
}
