import PluginManager from "../Libs/PluginManager";
import MarkdownRender from "../plugins/MarkdownRender";
import FixSrcPlugin from "../plugins/FixSrcPlugin";
import FixLinkPlugin from "../plugins/FixLinkPlugin";

export default function configurePlugin(pluginManager: PluginManager, baseWebUrl: string) {
    pluginManager.registerPlugin(new MarkdownRender());
    pluginManager.registerPlugin(new FixSrcPlugin(baseWebUrl));
    pluginManager.registerPlugin(new FixLinkPlugin());
}
