import MarkdownIt from "markdown-it";
import PluginInterface from "../interfaces/PluginInterface";
import ArticleEntity from "../entities/ArticleEntity";
import * as path from "path";

export default class MarkdownRender implements PluginInterface {

    onAfterRead(articleEntity: ArticleEntity): void {
        let ext = path.extname(articleEntity.id);
        let isMdExt = ext === '.md';
        let isMdMime = articleEntity.mimeType === 'text/markdown';
        if(isMdExt || isMdMime) {
            let md = new MarkdownIt();
            articleEntity.content = md.render(articleEntity.content);
        }
    }
}
