import path from "path";
import PluginInterface from "../interfaces/PluginInterface";
import ArticleEntity from "../entities/ArticleEntity";

export default class FixLinkPlugin implements PluginInterface {

    onAfterRead(articleEntity: ArticleEntity): void {
        let dir = path.dirname(articleEntity.id);
        let baseUri = '#/md-reader/article/' + dir;
        articleEntity.content = articleEntity.content.replace(/href="(?!http)(.*)"/giu, 'href="' + path.normalize(baseUri + '/$1') + '"');
    }
}
