import diConfigurator from "../../../../../packages/core/container/singletons/diConfigurator";
import ArticleRpcRepository from "../repositories/rpc/ArticleRepository";
import ArticleArrayRepository from "../repositories/array/ArticleRepository";
import ArticleService from "../services/ArticleService";
import PluginManager from "../Libs/PluginManager";
import RestClient from "../Libs/RestClient";
import axios from "axios";
import configurePlugin from "./plugin";
import configManager from "../../../../core/configManager/singletons/configManager";

export default function configureDi(domainName: string) {
    let bundleDiConfigurator = diConfigurator.createInstance(domainName);

    let baseWebUrl = '/docs/';
    let pluginManager = new PluginManager();
    configurePlugin(pluginManager, baseWebUrl);

    let articleRepository;
    let collection = configManager.get('staticData.md-reader.collection');
    if(collection) {
        articleRepository = createArrayRepository(collection);
    } else {
        articleRepository = createRpcRepository(baseWebUrl);
    }

    bundleDiConfigurator.bind("services.article", new ArticleService(null, articleRepository, pluginManager));
}

function createRpcRepository(baseWebUrl: string) {
    const axiosInstance = axios.create();
    axiosInstance.defaults.baseURL = baseWebUrl;
    let restClient = new RestClient(axiosInstance);
    return new ArticleRpcRepository(restClient);
}

function createArrayRepository(collection) {

    // console.log(collection);

    return new ArticleArrayRepository(collection);
}
