import Query from "../../../../../core/domain/libs/Query";
import path from "path";
import _ from "lodash";
import BaseCrudArrayRepository from "../../../../../core/domain/base/array/BaseCrudArrayRepository";

export default class ArticleRepository extends BaseCrudArrayRepository {

    constructor(collection) {
        super();
        this.collection = collection;
    }

    async oneById(id, query: Query = null) {
        id = ArticleRepository.canonicalPath(id);
        id = _.trim(id, '/');
        return super.oneById(id, query);
    }

    private static canonicalPath(file: string): string {
        if (!file) {
            file = '';
        }
        file = _.trim(file, '/');
        if (!path.extname(file)) {
            file = file + '/README.md';
        }
        return file;
    }
}
