import path from "path";
import PluginInterface from "../interfaces/PluginInterface";
import ArticleEntity from "../entities/ArticleEntity";

/**
 * Фикс путей к статическим ресурсам.
 */
export default class FixSrcPlugin implements PluginInterface {

    constructor(private readonly baseWebUrl) {
    }

    onAfterRead(articleEntity: ArticleEntity): void {
        let dir = path.dirname(articleEntity.id);
        articleEntity.content = articleEntity.content.replace(/src="(?!http)(.*)"/giu, 'src="' + this.baseWebUrl + '/' + dir + '/$1"');
    }
}
