import TemplateHelper from "../../../../ext/string/helpers/TemplateHelper";

export default class HelpEntityHelper {

    private static renderList(list, vars) {
        for (let i in list) {
            if (list.hasOwnProperty(i)) {
                let it = list[i];
                list[i] = TemplateHelper.render(it, vars);
            }
        }
        return list;
    }

    public static prepareEntity(domainName, entity) {
        let vars = {
            domainName,
        };
        entity.name = TemplateHelper.render(entity.name, vars);
        entity['id'] = entity.name;
        vars['name'] = entity.name;
        vars['id'] = entity.name;
        vars['title'] = entity.title ? TemplateHelper.render(entity.title, vars) : entity.name;
        if (entity.menu) {
            entity.menu = HelpEntityHelper.renderList(entity.menu, vars);
        }
        if (entity.breadcrumbs) {
            entity.breadcrumbs = HelpEntityHelper.renderList(entity.breadcrumbs, vars);
        }
        if (entity.content) {
            entity.content = TemplateHelper.render(entity.content, vars);
        }
        return entity;
    }
}