export default class TemplateHelper {

    static render(template, vars, begin = '{{', end = '}}') {
        for (let varName in vars) {
            if (vars.hasOwnProperty(varName)) {
                let varValue = vars[varName];
                let placeholder = begin + varName + end;
                template = template.replace(placeholder, varValue);
            }
        }
        return template;
    }
}
