import reactStringReplace from "react-string-replace";
import React from "react";
import Html from "../../../../../widgets/html/Html";

function prepare(content, HelpLink) {
    const exp = /<a href="help:\/\/(.+)">.*<\/a>/g;
    const callback = (match, i) => (
        <HelpLink key={i} name={match}/>
    );
    return reactStringReplace(content, exp, callback);
}

export default function ItemContent(props) {
    let contentArr = prepare(props.content, props.linkWidget);
    return (
        <>
            {contentArr.map(function (it, index) {
                if (typeof it == 'string') {
                    it = <Html key={index}>{it}</Html>;
                }
                return it;
            })}
        </>
    );
}
